<template>

    <v-card-text>
        <v-text-field label="ENS address" v-model="ensValue" :rules="ensRules" @keyup.enter="resolve" required>
            <v-tooltip slot="prepend" color="info" bottom>
                <template v-slot:activator="{ on, attrs }">
                    <v-icon v-bind="attrs" v-on="on" small @click="copyValue(ensValue)">mdi-content-copy</v-icon>
                </template>
                <span>Copy to clipboard</span>
            </v-tooltip>
        </v-text-field>

        <v-text-field label="Ethereum address" v-model="addrValue" :rules="addrRules" class="text-body-2" @keyup.enter="resolve" required readonly>
            <v-tooltip slot="prepend" color="info" bottom>
                <template v-slot:activator="{ on, attrs }">
                    <v-icon v-bind="attrs" v-on="on" small @click="copyValue(addrValue)">mdi-content-copy</v-icon>
                </template>
                <span>Copy to clipboard</span>
            </v-tooltip>
        </v-text-field>
        <div class="d-flex justify-end">
            <v-btn color="primary" text @click="resolve" rounded>Resolve</v-btn>
        </div>
    </v-card-text>

</template>

<script>

    import { mapState, mapActions } from 'vuex';

    export default {
        name: 'ENS',

        data() {
            return {
                ensValue: '',
                ensRules: [],

                addrValue: '',
                addrRules: []
            }
        },

        computed: {
            ...mapState({
                'w3': state => state.chain.w3
            })
        },

        watch: {
            ensValue() {
                this.addrValue = '';
            }
        },

        methods: {
            ...mapActions({
                'displayAlert': 'alert/displayAlert'
            }),

            copyValue(val) {
                if (navigator.clipboard) {
                    navigator.clipboard.writeText(val);
                }
            },

            resolve() {
                this.w3.eth.ens.getAddress(this.ensValue)
                    .then( addr => this.addrValue = addr )
                    .catch( err => this.displayAlert({ message: 'Record not found', type: 'info' }) );
            }
        }
    }

</script>
